.profile-card {
    transition: 0.5s;
}
.profile-card:hover {
    transform: scale(1.1);
    transition: 0.5s;
}
.profile-photo {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    border-radius: 100%;
    overflow: hidden;
    background-color:red;
}
.social-links ul {
    padding: 0px;
    margin: 0px;
}
.social-links ul li {
    list-style: none;
    display: inline-block;
    margin: 2px 0px;
    font-size: 20px;            
}
.social-links ul li a {
    color: #333;
    padding: 2px 6px;
    border-radius: 5px;
    transition: 0.5s;
}
.social-links ul li:hover a {
    background-color: #333333;
    color: #fff;
    transition: 0.5s;
}

.size{
    width: 600px;
    height: 400px;
    margin-left: 22%;
    margin-top: 50px;
}

.loguito{
    width: 100px;
    height: 100px;
